export default function cfmToErb(text, mode = "erb") {
  return [
    convert_cfif,
    convert_cfswitch,
    convert_cfset,
    convert_p_variable
  ].reduce((m, cb) => {
    return cb(m, mode);
  }, text);
}

function convert_cfif(text, mode = "erb") {
  if(mode === "rb") {
    return text.replaceAll(/<cfif ([\s\S]*?)>/g, (match, p1) => {
      return `if ${expression_in_tag(p1)}`;
    }).replaceAll(/<cfelseif ([\s\S]*?)>/g, (match, p1) => {
      return `elsif ${expression_in_tag(p1)}`;
    }).replaceAll(/<cfelse>/g, "else")
      .replaceAll(/<\/cfif>/g, "end")
  }

  return text.replaceAll(/<cfif ([\s\S]*?)>/g, (match, p1) => {
    return `<% if ${expression_in_tag(p1)} %>`;
  }).replaceAll(/<cfelseif ([\s\S]*?)>/g, (match, p1) => {
    return `<% elsif ${expression_in_tag(p1)} %>`;
  }).replaceAll(/<cfelse>/g, "<% else %>")
    .replaceAll(/<\/cfif>/g, "<% end %>")
}

function expression_in_tag(text) {
  return text.trim()
    .replaceAll(/is equal/g, "==")
    .replaceAll(/ eq /g, " == ")
    .replaceAll(/ is not /g, " != ")
    .replaceAll(/not equal/g, "!=")
    .replaceAll(/ neq /g, " != ")
    .replaceAll(/greater than/g, ">")
    .replaceAll(/ gt /g, " > ")
    .replaceAll(/less than/g, "<")
    .replaceAll(/ lt /g, " < ")
    .replaceAll(/greater than or equal to/g, ">=")
    .replaceAll(/ gte /g, " >= ")
    .replaceAll(/ ge /g, " >= ")
    .replaceAll(/less than or equal to/g, "<=")
    .replaceAll(/ lte /g, " <= ")
    .replaceAll(/ le /g, " <= ")
    .replaceAll(/ is /g, " == ")
    .replaceAll(/ and /g, " && ")
    .replaceAll(/ or /g, " || ");
}

function convert_cfswitch(text, mode = "erb") {
  if(mode === "rb") {
    return text.replaceAll(/<cfswitch expression="#(.*?)#">/g, "case $1")
      .replaceAll(/<cfcase value="(.*?)">/g, "when $1")
      .replaceAll(/<\/cfcase>/g, "")
      .replaceAll(/<cfdefaultcase>/g, "else")
      .replaceAll(/<\/cfdefaultcase>/g, "")
      .replaceAll(/<\/cfswitch>/g, "end")
  }

  return text.replaceAll(/<cfswitch expression="#(.*?)#">/g, "<% case $1 %>")
    .replaceAll(/<cfcase value="(.*?)">/g, "<% when $1 %>")
    .replaceAll(/<\/cfcase>/g, "")
    .replaceAll(/<cfdefaultcase>/g, "<% else %>")
    .replaceAll(/<\/cfdefaultcase>/g, "")
    .replaceAll(/<\/cfswitch>/g, "<% end %>")
}

function convert_cfset(text, mode = "erb") {
  if(mode === "rb") {
    return text.replaceAll(/<cfset (.*?)=([\s\S]*?)>/g, (match, p1, p2) => {
      return `${p1.trim()} = ${expression_in_tag(p2)}`;
    });
  }

  return text.replaceAll(/<cfset (.*?)=([\s\S]*?)>/g, (match, p1, p2) => {
    return `<% ${p1.trim()} = ${expression_in_tag(p2)} %>`;
  });
}

function convert_p_variable(text, mode) {
  if(mode === "rb") {
    return text;
  }

  return text.replaceAll(/<%(.*?)%>/g, (_, p1) => {
    return "<%" + p1.replaceAll(/"#([a-zA-Z][\w._]+)#"/g, "$1") + "%>"
  });
}
